:root {
  --color: 16777215;
}

.App {
  text-align: center;
  margin-top: 2em;
}

body {
  margin: 0 2em;
}

.load {
  margin: 40vh auto;
  text-align: center;
}

.load h1:after {
  content: '. ';
  animation: dots 1s steps(5, end) infinite;}

@keyframes dots {
  0%, 20% {
    color: rgba(0,0,0,0);
    text-shadow:
      .25em 0 0 rgba(0,0,0,0),
      .5em 0 0 rgba(0,0,0,0);}
  40% {
    color: black;
    text-shadow:
      .25em 0 0 rgba(0,0,0,0),
      .5em 0 0 rgba(0,0,0,0);}
  60% {
    text-shadow:
      .25em 0 0 black,
      .5em 0 0 rgba(0,0,0,0);}
  80%, 100% {
    text-shadow:
      .25em 0 0 black,
      .5em 0 0 black;
}}

h1 {
  font-size: 1.5em;
  margin-bottom: 0;
}

h2 {
  font-weight: 500;
  margin-top: 0;
  font-size: 1.25em;
}

.items {
  flex: 0 1 2%;
  height: 0;
  padding-bottom: calc(100%/50);
  display: inline-block;
}

#content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.legend {
  width: 100%;
  height: 0;
  padding-bottom: 100%;
  display: inline-block;
}

#legend {
  margin: 2em auto 3em;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

#legend h2 {
  font-weight: 700;
  flex: 1 0 100%;
  margin-bottom: 0;
}

#legend h3 {
  margin-top: 0;
  flex: 1 0 100%;
  font-size: 1em;
  font-weight: 500;
}

#legend div {
  flex: 0 1 20%;
  margin: 0 0.5em;
}

h4 {
  font-size: .75em;
  font-weight: 500;
}

@media only screen and (min-width: 400px) {
  #legend div {
    flex: 0 1 15%;
    margin: 0 0.5em;
  }

} @media only screen and (min-width: 700px) {
  #legend div {
    flex: 0 1 12%;
    margin: 0 0.5em;
  }

  #legend {
    width: 90%;
  }

} @media only screen and (min-width: 900px) {
  body {
    margin: 0 5em;
  }

  h1 {
    font-size: 2em;
  }

  #legend div {
    flex: 0 1 8%;
    margin: 0 0.5em;
  }

} 